import React,{ Component } from 'react';
import firebase from './../../firebase';
import moment from 'moment';
import Fileupload from './fileupload';

import $ from 'jquery';
import CKEditor from 'react-ckeditor-component';
import Comments from './comments';

export default class AddPost extends Component {
  constructor(props) {
      super(props);
      this.state = {
        allPosts: [],
        allMyPosts: [],
        countPosts: 0,
        allCategory: [],
        categoryID: 1,
        contentck: '',
        currentPage: 1,
        itemsPerPage: 5,
        pageNumberLimit: 3,
        maxPageNumberLimit: 3,
        minPageNumberLimit: 0,
        pages: [],
        indexOfLastItem: 0,
        indexOfFirstItem: 0,
        currentItems: []
      }
      this.title = React.createRef();
      this.description = React.createRef();
      this.content = React.createRef();
      this.select = React.createRef();
      this.categ = '';
    }

  componentDidMount() {
    //alert("Show Posts");
    this.setState({
      indexOfLastItem: this.state.currentPage * this.state.itemsPerPage,
      indexOfFirstItem: this.state.indexOfLastItem - this.state.itemsPerPage
    });
    firebase.database().ref("/category").orderByChild('id')
        .on('value', snapshot => {
            let category = [];
            snapshot.forEach(child => {
            var data = child.val();
            category.push({id: data.id,title: data.title,description: data.description,time: data.time});
            });
            this.setState({ 
                allCategory: category,
                countCategory: category.length
            });
    });
    firebase.database().ref("/posts").orderByChild('id')
      .on('value', snapshot => {
          let posts = [];
          let myposts = [];
          snapshot.forEach(child => {
          var data = child.val();
          posts.push({id: data.id,title: data.title,description: data.description,content: data.content,user_id: data.user_id,category_id: data.category_id,image: data.image,time: data.time});
          if(data.user_id == localStorage.getItem("user_id")){
          myposts.push({id: data.id,title: data.title,description: data.description,content: data.content,user_id: data.user_id,category_id: data.category_id,image: data.image,time: data.time});
          }
          });
          //setAllPosts(posts);
          //setCountPosts(posts.length);
          //setAllMyPosts(myposts);
          var pages = [];
          for (let i = 1; i <= Math.ceil(myposts.length / this.state.itemsPerPage); i++) {
            pages.push(i);
          }
          setTimeout(function(){ 
          this.setState({ 
            allPosts: posts,
            allMyPosts: myposts,
            countPosts: posts.length,
            currentItems: myposts.slice(this.state.indexOfFirstItem<0?0:this.state.indexOfFirstItem, this.state.indexOfLastItem), 
            pages: pages
          });
          }.bind(this), 200);
          //console.log(myposts);
          //console.log(this.state.currentItems);
          //console.log(this.state.indexOfFirstItem<0?0:this.state.indexOfFirstItem);
          //console.log(this.state.indexOfLastItem);
          //console.log(myposts.slice(this.state.indexOfFirstItem<0?0:this.state.indexOfFirstItem, this.state.indexOfLastItem));

          setTimeout(function(){ 
              if(this.state.countPosts>2){
                $(".container-fluid").css("position","relative");
              }else{
                $(".container-fluid").css("position","fixed");
              }
            }.bind(this), 300);
      });
      CKEditor.config = {'toolbar':'document'};
  }

  handleClick(event){
    //alert(Number(event.target.id));
     
    this.setState({ 
      currentPage: Number(event.target.id)
    });
    setTimeout(function(){ 
    //console.log(this.state.currentPage);
    //console.log(this.state.pages.length);
    if(this.state.currentPage == this.state.pages.length){
      //console.log(this.state.itemsPerPage*(this.state.currentPage-1));
      //console.log(this.state.itemsPerPage*this.state.currentPage);
      this.setState({
        currentItems: this.state.allPosts.slice(this.state.itemsPerPage*(this.state.currentPage-1), this.state.itemsPerPage*this.state.currentPage)
      });
    }else{
    this.setState({
      currentItems: this.state.allPosts.slice(this.state.itemsPerPage*this.state.currentPage, this.state.itemsPerPage*(this.state.currentPage + 1))
    });
    }
    }.bind(this), 200);

    setTimeout(function(){ 
      if(this.state.countPosts>2){
       $(".container-fluid").css("position","relative");
         }else{
           $(".container-fluid").css("position","fixed");
         }
       }.bind(this), 300);
  }

  handleNextbtn(){
    this.setState({ 
      currentPage: (this.state.currentPage + 1)
     });

    if (this.state.currentPage + 1 > this.state.maxPageNumberLimit) {
      this.setState({ 
        maxPageNumberLimit: (this.state.maxPageNumberLimit + this.state.pageNumberLimit),
        minPageNumberLimit: (this.state.minPageNumberLimit + this.state.pageNumberLimit)
       });
    }
    this.setState({
      currentItems: this.state.allMyPosts.slice(this.state.itemsPerPage*this.state.currentPage, this.state.itemsPerPage*(this.state.currentPage + 1))
    });

    setTimeout(function(){ 
      if(this.state.countPosts>2){
       $(".container-fluid").css("position","relative");
         }else{
           $(".container-fluid").css("position","fixed");
         }
       }.bind(this), 300);
  }

  handlePrevbtn(){
    this.setState({ 
      currentPage: (this.state.currentPage - 1)
     });

    if ((this.state.currentPage - 1) % this.state.pageNumberLimit == 0) {
      this.setState({ 
        maxPageNumberLimit: (this.state.maxPageNumberLimit - this.state.pageNumberLimit),
        minPageNumberLimit: (this.state.minPageNumberLimit - this.state.pageNumberLimit)
       });
    }
    var indexmin = (this.state.itemsPerPage*(this.state.currentPage - 2))<0?0:(this.state.itemsPerPage*(this.state.currentPage - 2));
    var indexmax = this.state.itemsPerPage*(this.state.currentPage - 1);
    //console.log(indexmin);
    //console.log(indexmax);
    this.setState({
      currentItems: this.state.allMyPosts.slice(indexmin, indexmax)
    });

    setTimeout(function(){ 
      if(this.state.countPosts>2){
       $(".container-fluid").css("position","relative");
         }else{
           $(".container-fluid").css("position","fixed");
         }
       }.bind(this), 300);
  }

  handleLoadMore(){
    this.setState({ 
      itemsPerPage: (this.state.itemsPerPage + 5)
     });
     this.setState({
      currentItems: this.state.allMyPosts.slice(this.state.itemsPerPage*(this.state.currentPage - 1), this.state.itemsPerPage*(this.state.currentPage + 1))
    });

    setTimeout(function(){ 
      if(this.state.countPosts>2){
       $(".container-fluid").css("position","relative");
         }else{
           $(".container-fluid").css("position","fixed");
         }
       }.bind(this), 300);
  }

  addpost(){
    if(this.title.current.value.length>1 && this.description.current.value.length>1){
      var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
      var refdb = firebase.database().ref('/posts').push();
        refdb = refdb.toString().slice(-20);
        setTimeout(function(){ 
        let updateData = firebase.database().ref("/posts").child(refdb);
        updateData.child("id").set(this.state.countPosts+1);
        updateData.child("user_id").set(localStorage.getItem("user_id"));
        updateData.child("category_id").set(this.state.categoryID);
        updateData.child("title").set(this.title.current.value);
        updateData.child("description").set(this.description.current.value);
        updateData.child("content").set(localStorage.getItem("contentck"));
        updateData.child("image").set(localStorage.getItem("image_url"));
        updateData.child("time").set(curtime);
      }.bind(this), 300);
      //setCountPosts(this.countPosts+1);
      this.setState({ 
        countPost: this.countPosts+1
      });
      alert("Add post done!");
    } else {
      alert("Please fill the Title and Description fields!");
    }
  }

  selectchange(){
    //alert("Select Change"+this.select.current.value);
    this.setState({ 
      categoryID: this.select.current.value
    });
  }

  onChangeCK(evt){
    //console.log("onChange fired with event info: ", evt);
    var newContent = evt.editor.getData();
    localStorage.setItem("contentck",newContent);
    //this.setState({
    //  contentck: newContent
    //});
    //console.log(this.state.contentck);
  }
  
  onBlur(evt){
    console.log("onBlur event called with event info: ", evt);
  }
  
  afterPaste(evt){
    console.log("afterPaste event called with event info: ", evt);
  }

  render(){
    const bgstyle={
      backgroundColor: (localStorage.getItem('backgroundColor')== null)?"#282c34":localStorage.getItem("backgroundColor"),
      color: 'white'
    }
  
    const button={
      borderRadius: '15px',
      margin: '0 0 15px 0',
    }

    const imgst={
      marginBottom: '10px'
    }

    const renderPageNumbers = this.state.pages.map((number) => {
      if (number < this.state.maxPageNumberLimit + 1 && number > this.state.minPageNumberLimit) {
        return (
          <li
            key={number}
            id={number}
            onClick={(event)=>this.handleClick(event)}
            className={this.state.currentPage == number ? "active" : null}
          >
            {number}
          </li>
        );
      } else {
        return null;
      }
    });
  
    let pageIncrementBtn = null;
    if (this.state.pages.length > this.state.maxPageNumberLimit) {
      pageIncrementBtn = <><li onClick={()=>this.handleNextbtn()} >&hellip;</li><li id={this.state.pages.length} onClick={(event)=>this.handleClick(event)} className={(this.state.currentPage == this.state.pages.length)?"active":null} >{this.state.pages.length}</li></>;
    }
  
    let pageDecrementBtn = null;
    if (this.state.minPageNumberLimit >= 1) {
      pageDecrementBtn = <><li id={1} onClick={(event)=>this.handleClick(event)} className={(this.state.currentPage == 1)?"active":null} >1</li><li onClick={()=>this.handlePrevbtn()} > &hellip;</li></>;
    }

  return (
      <div className="container" style={bgstyle}>
        <h2>This is Add Posts Page</h2>
        <div className="col-9 col-sm-9 col-md-9 col-lg-9 col-xl-9 mx-auto">
        <div className="form-group">
          <label htmlFor="title" className="sr-only">Title</label>
          <input ref={this.title} type="text" id="title" className="form-control" placeholder="Title" name="title" required autoFocus />
          <div className="valid-feedback"></div>
          <div className="invalid-feedback">Please fill out this field.</div>
        </div>
        <div className="form-group">
          <label htmlFor="description" className="sr-only">Description</label>
          <input ref={this.description} type="text" id="description" className="form-control" placeholder="Description" name="description" required autoFocus />
          <div className="valid-feedback"></div>
          <div className="invalid-feedback">Please fill out this field.</div>
        </div>
        <div className="form-group">
          <select className="custom-select" ref={this.select} onChange={()=>this.selectchange()}>
          {this.state.allCategory.map((item, index) => (<option value={item.id}>{item.title}</option>))}
          </select>
        </div>
        <div className="form-group">
          <label htmlFor="content" className="sr-only">Content</label>
          <CKEditor 
            activeClass="p10" 
            content={this.state.contentck} 
            events={{
                "blur": this.onBlur,
                "afterPaste": this.afterPaste,
                "change": this.onChangeCK
            }}
            />
          <div className="valid-feedback"></div>
          <div className="invalid-feedback">Please fill out this field.</div>
        </div>
        <Fileupload />
        <button className="btn btn-primary" type="submit" style={button} onClick={()=>this.addpost()}>Add</button>
        </div>
        {this.state.currentItems.map((item, index) => (<div className="col-9 col-sm-9 col-md-9 col-lg-9 col-xl-9 mx-auto">
        <img className="img-fluid" src={"https://firebasestorage.googleapis.com/v0/b/reactblog-824c0.appspot.com/o/image%2F"+item.image+"?alt=media"} style={imgst}/>
          <p className="text-left">{item.title}</p>
          <p className="text-left">{item.description}</p>
          {this.state.allCategory.forEach((itemcateg)=>{
            if(itemcateg.id == item.category_id){
              this.categ = itemcateg.title;
            }
          })}
          <p className="text-left">Category: {this.categ}</p>
          <p className="text-left"><div dangerouslySetInnerHTML={{__html: item.content}} /></p>
          <p className="text-left">{item.time}</p>
          <Comments post_id={item.id}/></div>))}

          <div className="row">
            <div className="row mx-auto">
          <ul className="pageNumbers">
          <li>
            <button
              onClick={()=>this.handlePrevbtn()}
              disabled={this.state.currentPage == this.state.pages[0] ? true : false}
            >
              <sup>&#8592;</sup>
            </button>
          </li>
          {pageDecrementBtn}
          {renderPageNumbers}
          {pageIncrementBtn}

          <li>
            <button
              onClick={()=>this.handleNextbtn()}
              disabled={this.state.currentPage == this.state.pages[this.state.pages.length - 1] ? true : false}
            >
              <sup>&#8594;</sup>
            </button>
          </li>
        </ul>
        <button onClick={()=>this.handleLoadMore()} className="loadmore">
          <sup>&hellip;</sup>
        </button>
        </div>
        </div>

      </div>)
  }
}